import goodsHeader from './goodsHeader.vue';
import popupSpec from '../../components/popupSpec/popupSpec.vue';
import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			isKeep: false, // 收藏
			goodsData: {
				swiperList: [],
				spec: [],
				comment: [
					{
						face: '',
						content: '',
						username: ''
					}
				],
				descriptionStr: ''
			},
			goodsInfo: {
				name: '',
				price: '',
				number: 1,
				spec: ''
			},
			currentSwiper: 0, // 轮播图下标
			spaceInfo: {
				showSpace: false
			}
		};
	},
	onLoad(option) {
		if (!option.goodsInfo) {
			uni.switchTab({
				url: '../tabBar/category/category'
			});
			return;
		}
		this.goodsInfo = JSON.parse(option.goodsInfo);
		this.initData();
	},
	components: {
		goodsHeader,
		popupSpec
	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getGoods,
				success: res => {
					this.goodsData = res.data;
				}
			});
		},
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		setSelectSpec(item) {
			// 选择规格
			this.goodsInfo.spec = item;
		},
		hideSpec() {
			this.spaceInfo.showSpace = false;
		},
		add() {
			this.goodsInfo.number++;
		},
		sub() {
			if (this.goodsInfo.number <= 1) {
				return;
			}
			this.goodsInfo.number--;
		},
		handleRatings() {
			//本地存储
			uni.setStorageSync('comments', this.goodsData.comment);
			uni.navigateTo({
				url: './ratings'
			});
		},
		keep() {
			this.isKeep = !this.isKeep;
			if (this.isKeep) {
				uni.showToast({
					icon: 'success',
					title: '收藏成功'
				});
			} else {
				uni.showToast({
					icon: 'success',
					title: '取消收藏'
				});
			}
		},
		joinCart() {
			// console.log(this.goodsInfo)

			//存错到本地存储里
			//1.先去本地存储中取数据
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					let goodsList = res.data;
					let isExist = false;
					goodsList.forEach(goods => {
						if (goods.goods_id == this.goodsInfo.goods_id && goods.spec == this.goodsInfo.spec) {
							// 如果存在  修改商品数量
							goods.number += Number(this.goodsInfo.number);
							isExist = true;
						}
					});
					if (!isExist) {
						// 不存在，存入数组
						goodsList.push(this.goodsInfo);
					}
					this.setGoodsList(goodsList);
				},
				fail: error => {
					//没有得到数据，那么就存数据
					let goodsList = [];
					goodsList.push(this.goodsInfo);
					//往本地存储数据
					this.setGoodsList(goodsList);
				}
			});
		},
		setGoodsList(goodsList) {
			//存储到本地存储中
			uni.setStorage({
				key: 'goodsList',
				data: goodsList,
				success: () => {
					uni.showToast({
						icon: 'success',
						title: '添加购物车成功'
					});
				}
			});
		},
		handleBuy() {
			let tempList = [];
			tempList.push(this.goodsInfo);
			uni.setStorage({
				key: 'confirmList',
				data: tempList,
				success: () => {
					uni.navigateTo({
						url: '../order/order'
					});
				}
			});
		}
	}
};
